@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Urbanist";
  font-display: swap;
  src: url("../fonts/Urbanist-Regular.woff2") format("woff2"), url("../fonts/Urbanist-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Urbanist";
  font-display: swap;
  src: url("../fonts/Urbanist-Medium.woff2") format("woff2"), url("../fonts/Urbanist-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Urbanist";
  font-display: swap;
  src: url("../fonts/Urbanist-SemiBold.woff2") format("woff2"), url("../fonts/Urbanist-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Urbanist";
  font-display: swap;
  src: url("../fonts/Urbanist-Bold.woff2") format("woff2"), url("../fonts/Urbanist-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Urbanist";
  font-display: swap;
  src: url("../fonts/Urbanist-Black.woff2") format("woff2"), url("../fonts/Urbanist-Black.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Urbanist", "Arial", sans-serif;
  color: #1f1f1f;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.btn-reset {
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1660px;
  margin: 0 auto;
  padding: 0 70px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0);
}
.header.is-active .header__logo svg {
  color: #037280;
}
.header.is-active .main-nav__toggle {
  width: 26px;
  height: 26px;
}
.header.is-active .main-nav__line {
  background-color: #1f1f1f;
}
.header.is-active .main-nav__line:nth-child(1) {
  top: 13px;
  transform: rotate(-45deg) translate(0, -50%);
}
.header.is-active .main-nav__line:nth-child(2) {
  transform: translateX(100%);
  opacity: 0;
  visibility: hidden;
}
.header.is-active .main-nav__line:nth-child(3) {
  top: 11px;
  transform: rotate(45deg) translate(0, 50%);
}
.header.is-active .main-nav__list {
  transform: translateX(0);
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
@media (max-width: 767px) {
  .header__wrapper {
    align-items: center;
    padding: 8px 0;
  }
}
.header__logo {
  z-index: 11;
  position: relative;
  width: 152px;
  height: 102px;
  margin-top: 20px;
}
@media (max-width: 767px) {
  .header__logo {
    width: 74px;
    height: 51px;
    margin-top: 0;
  }
}
.header__logo svg {
  display: block;
  width: 152px;
  height: 102px;
  color: #ffffff;
  transition: color 0.3s ease;
}
@media (max-width: 767px) {
  .header__logo svg {
    width: 74px;
    height: 51px;
  }
}
.header__phone {
  display: block;
  align-self: flex-start;
  padding: 22px 50px;
  font-weight: 700;
  font-size: 17px;
  line-height: 25px;
  border-radius: 0 0 10px 10px;
  background-color: rgba(255, 255, 255, 0.24);
  color: #ffffff;
  backdrop-filter: blur(4.5px);
  transition: color 0.3s ease;
}
@media (max-width: 1023px) {
  .header__phone {
    margin: 0 20px;
  }
}
@media (max-width: 767px) {
  .header__phone {
    align-self: center;
    padding: 0;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
    border: none;
    background-color: rgba(255, 255, 255, 0);
  }
}
@media (hover: hover) {
  .header__phone:hover:not(.focus-visible) {
    color: #c7de2e;
  }
}
.header__phone.focus-visible:focus {
  color: #c7de2e;
}

.main-nav {
  margin: 0 20px;
}
@media (max-width: 1023px) {
  .main-nav {
    order: 1;
    display: flex;
    justify-content: center;
    width: 53px;
    margin: 0;
  }
}
@media (max-width: 767px) {
  .main-nav {
    width: 35px;
  }
}
.main-nav__toggle {
  z-index: 11;
  position: relative;
  display: none;
  width: 53px;
  height: 24px;
  margin-top: 23px;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
@media (max-width: 1023px) {
  .main-nav__toggle {
    display: block;
  }
}
@media (max-width: 767px) {
  .main-nav__toggle {
    width: 35px;
    line-height: 16px;
    margin-top: 0;
  }
}
@media (hover: hover) {
  .main-nav__toggle:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__toggle.focus-visible:focus {
  opacity: 0.8;
}
.main-nav__line {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  transition: color 0.3s ease, transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
}
.main-nav__line:nth-child(1) {
  top: 0;
}
.main-nav__line:nth-child(2) {
  top: 50%;
}
.main-nav__line:nth-child(3) {
  bottom: 0;
}
.main-nav__list {
  display: flex;
  margin: 0;
  padding: 25px 50px;
  border-radius: 0 0 10px 10px;
  background: rgba(255, 255, 255, 0.24);
  backdrop-filter: blur(4.5px);
  list-style: none;
}
@media (max-width: 1023px) {
  .main-nav__list {
    z-index: 10;
    position: fixed;
    flex-direction: column;
    justify-content: flex-start;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    padding: 126px 40px 30px;
    background-color: #ffffff;
    transition: transform 0.3s ease;
    transform: translateX(100%);
  }
}
@media (max-width: 767px) {
  .main-nav__list {
    padding: 126px 20px 30px;
  }
}
.main-nav__item {
  margin-right: 51px;
}
@media (max-width: 1199px) {
  .main-nav__item {
    margin-right: 30px;
  }
}
@media (max-width: 1023px) {
  .main-nav__item {
    margin-right: 0;
  }
}
.main-nav__item:last-child {
  margin-right: 0;
}
.main-nav__link {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  color: #ffffff;
  transition: color 0.3s ease;
  transform: translate3d(0, 0, 0);
}
@media (max-width: 1023px) {
  .main-nav__link {
    justify-content: space-between;
    padding: 15px 0;
    font-size: 16px;
    line-height: 24px;
    color: #1f1f1f;
  }
}
.main-nav__link::after {
  content: none;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background-color: #208d9a;
  transition: width 0.3s ease;
}
.main-nav__link.is-active {
  color: #208d9a;
}
.main-nav__link.is-active::after {
  content: "";
  width: 100%;
}
.main-nav__link svg {
  display: none;
}
@media (max-width: 1023px) {
  .main-nav__link svg {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 14px;
    height: 14px;
    margin-left: 20px;
  }
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    color: #c7de2e;
  }
}
.main-nav__link.focus-visible:focus {
  color: #c7de2e;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  padding: 17px 20px;
  font-weight: 600;
  font-size: 16px;
  line-height: 21px;
  border: none;
  border-radius: 5px;
  background-color: #c7de2e;
  color: #1f1f1f;
  cursor: pointer;
  user-select: none;
  transition: background-color 0.3s ease;
  outline: none;
}
@media (max-width: 767px) {
  .btn {
    padding: 13px 20px;
    font-size: 15px;
    line-height: 19px;
  }
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    background-color: #d5f021;
  }
}
.btn.focus-visible:focus {
  background-color: #d5f021;
}

.intro {
  position: relative;
  min-height: 759px;
  border-radius: 0 0 20px 20px;
}
