  .intro {
    min-height: 599px;
  }
}
@media (max-width: 767px) {
  .intro {
    min-height: 531px;
  }
}
.intro__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
